.tab-wrapper
  display: flex
  flex-direction: column
  align-items: center
  width: 100%
  max-width: 1440px
  padding: 10px
  background-color: lighten(lightgray, 10%)
  border-radius: 5px
  box-shadow: 0 2px 10px rgba(0,0, 0,.3)

.tab-nav
  position: relative
  display: flex
  width: 100%

  &_link
    position: relative
    flex-grow: 1
    background-color: #d1d4d4
    color: #716f6f
    padding: 10px
    text-align: center
    margin: 0 5px
    border: 1px solid transparent
    //border-top-left-radius: 5px
    //border-top-right-radius: 5px
    border-radius: 5px
    transition: border .4s ease-in-out, background-color .2s ease-in-out
    cursor: pointer

  &_link:hover
    background-color: rgba(0,139,139,.3)

  &_decorator
    position: absolute
    bottom: 0
    left: 0
    height: 3px
    transition: width .2s linear 0s, transform .2s ease-out 0s
    //background-color: cornflowerblue
    background-color: darkcyan
    //border-radius: 5px
    border-bottom-left-radius: 5px
    border-bottom-right-radius: 5px

.tab-container
  position: relative
  width: 100%
  margin-top: 20px
  height: 200px
  max-width: 1440px
  overflow: hidden

.tab-switch
  display: flex
  width: 9999px
  height: 100%
  transform: translateX(0)
  transition: transform .5s ease-in-out

.tab-item
  width: 100%
  max-width: 1440px
  color: #716f6f


